.cursor{
    cursor:pointer;
}
.btn:hover{
    background-color: green;
}
.btn{
   margin-left:125px;
}
.card-title{
    text-align: center;
  font-weight:bold;
}
.card-text{
    text-align: center;
}
.front{
    height:40vh;
}
.card{
    padding:0px;
    width:25em;
}


.card:hover{
    border:3px solid rgba(0, 4, 255, 0.562);
    background-color:rgba(128, 128, 128, 0.089);
    
    
}
.card-body:hover{
    font-size:18px;
}
img{
    padding:0px;
}

 @media only screen and (max-width:1206px )
{
    .card{
       
        width:20em;
    }
    .btn{
    display:block;
     margin:auto;
     width:150px;
     }
     .front{
        height:96vh;
    }
} 

